import React from 'react'
import {Modal, Button} from "antd"
import { CloseOutlined } from '@ant-design/icons';
import './ModalInformativo.css'
import {
    abrirCerrarModalInformativoPromociones} from 'appRedux/actions/Promociones'
import {useDispatch, useSelector} from "react-redux";

const ModalInformativo = () => {

    const dispatch = useDispatch();
    const {mostrarModalInformativo} = useSelector(({promociones}) => promociones);


    return (
        <Modal
            width={1000}
            title={null}
            visible={mostrarModalInformativo}
            footer = {null}
            closeIcon = {
                <Button
                    style={{background:'#30BEEB', border:'none', marginTop:'10px', marginRight:'5px'}}
                    shape="circle" 
                    icon={<CloseOutlined style={{"color" : 'white'}} />} 
                    onClick={() => dispatch(abrirCerrarModalInformativoPromociones(!mostrarModalInformativo))}
                >
                </Button>
            }
        >
            <div id="contenedorModal">
                <h1 id="txt_tituloModal">PROMOCIONES</h1><br/>
                <div id="txt_contenidoModal">
                    Por el presente correo, cumplo con informarle las tcticas promocionales para este mes con el nmero de combos/soles a reconocer por actividad y monto mximo de reconocimiento en valor por actividad respectivos por cada mecnica. Dichos montos no podrn excederse de no recibir una aprobacin formal del rea de Trade copiando al buzn _KC, Cuida tu negocio (Cuidatunegocio.kc@kcc.com), dejando claro que yo como ejecutivo no podr aprobar acciones adicionales sin antes pasar por las aprobaciones respectivas.<br/><br/>
                    Recordar que estos pagos sern va notas de crdito, una vez compartido su cierre de ventas hasta el da 5 hbil del siguiente mes y se pagarn va Notas de Crdito en el mismo siguiente mes. Si no enva el cierre dentro del plazo establecido, no se le pagar las tcticas promocionales dentro del prximo mes, sino un mes despus de esto.<br/><br/>
                    De acuerdo a la nueva poltica de KC pongo en copia al correo interno _KC, Cuida tu negocio (Cuidatunegocio.kc@kcc.com), y estas acciones entran en validez desde la fecha 01 de Septiembre hasta fin de mes.
                </div>
            </div>
        </Modal>
    )
}

export default ModalInformativo
